* {
  padding: 0;
  margin: 0;
}

body {
  background: #ecf0f1;
}

.wrapper {
  max-width: 600px;
  padding: 40px 10px;
  margin: 0 auto;
  position: relative;
  text-align: center;
}

video {
  display: none;
}

canvas {
  width: 100%;
  display: block;
  background: #bdc3c7;
}

button {
  margin: 15px 0;
  padding: 3px 6px;
  font-size: 12px;
}

